import Form from 'react-bootstrap/Form';
import FormControl from 'react-bootstrap/FormControl';
import Button from 'react-bootstrap/Button';
import { Container } from 'react-bootstrap';
import { NavLink } from 'react-router-dom';

const Signup = () => (
    <Container style={{padding:'30px'}}>
      
        <Form className="col-lg-6 offset-lg-4"  style={{width:'450px',textAlign:'left',paddingTop:'30px',paddingBottom:'50px', border: '1px solid grey'}}>
        <h4 style={{textAlign:'center'}}>Sign Up On Spanav</h4>
  <Form.Group controlId="formBasicFirstname">
    <Form.Label>FirstName</Form.Label>
    <Form.Control type="text" />
   
  </Form.Group>

  <Form.Group controlId="formBasicLastName">
    <Form.Label>LastName</Form.Label>
    <Form.Control type="text" />
  </Form.Group>
  <Form.Group controlId="formBasicUsername">
    <Form.Label>Username</Form.Label>
    <Form.Control type="text" />
  </Form.Group>
  <Form.Group controlId="formBasicEmail">
    <Form.Label>Email</Form.Label>
    <Form.Control type="email" />
   
  </Form.Group>
  <Form.Group controlId="formBasicPassword">
    <Form.Label>Password</Form.Label>
    <Form.Control type="password" />
  </Form.Group>
  <Form.Group controlId="formBasicConfirmPassword">
    <Form.Label>Confirm Password</Form.Label>
    <Form.Control type="password" />
  </Form.Group>

  
    

  <Button type="submit" variant="warning" style={{width:'425px',color:'white'}}>
    SIGNUP
  </Button>
  
</Form>

        
      
      
    </Container>
  );
  export default Signup