import React from 'react';
import Navbar from 'react-bootstrap/Navbar';
import Nav from 'react-bootstrap/Nav';
import NavDropdown from 'react-bootstrap/NavDropdown';
import Button from 'react-bootstrap/Button';
import Courses from './Courses';
import { NavLink } from 'react-router-dom';
import {LinkContainer} from 'react-router-bootstrap';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Main from './Main';

import Blogs from './Blogs';

import Home from './Home';
import Events from './Events';
import AboutUs from './AboutUs';
import Signin from './Signin';
import Signup from './Signup';


const mysty={
  backgroundColor:'white',
}

const Navigbar = () => (
    
    <div>
<Navbar  collapseOnSelect expand="lg"  style = {{
                backgroundColor: '#F5F5F5',color:'00000080', fontSize:'15px',fontWeight:'bold',height:''
            
              }} sticky="top">
                <LinkContainer to="/"><Navbar.Brand><img style={{height:'50px'}}src="https://tejaswi-spanav.github.io/spanav/logo.png"></img></Navbar.Brand></LinkContainer>
  
  <Navbar.Toggle aria-controls="responsive-navbar-nav" />
  <Navbar.Collapse id="responsive-navbar-nav">
    <Nav className="mr-auto">
    </Nav>
    <Nav className="justify-content-end"  >
    <Nav.Item eventkey={1}>
    <LinkContainer to="/" ><Nav.Link>HOME</Nav.Link></LinkContainer>
    </Nav.Item>
    <Nav.Item>
    <LinkContainer to="/Courses"><Nav.Link to='/Courses'>COURSES</Nav.Link></LinkContainer>
    </Nav.Item>
    <Nav.Item>
    <LinkContainer to="/Blogs"><Nav.Link to='/Blogs'>BLOGS</Nav.Link></LinkContainer>
    </Nav.Item>
    <Nav.Item>
    <LinkContainer to="/Events"><Nav.Link to='/Events'>EVENTS</Nav.Link></LinkContainer>
    
    </Nav.Item>
    <Nav.Item>
    <LinkContainer to="/AboutUs"><Nav.Link to='/AboutUs'>ABOUT US</Nav.Link></LinkContainer>
    
    </Nav.Item>
    <Nav.Item>
    <Nav.Link to="/ContactUs">CONTACT US</Nav.Link>
    </Nav.Item>
    <Nav.Item>
    <NavDropdown title="BROWSE BY STREAM" id="collasible-nav-dropdown">
        <NavDropdown.Item href="#action/3.1">Coming Soon</NavDropdown.Item>
        
      </NavDropdown>
    </Nav.Item>
    <Nav.Item>
    <LinkContainer to="/Signup"><Nav.Link to='/Signup'><Button variant="warning">SIGNUP/LOGIN</Button>{' '}</Nav.Link></LinkContainer>
    
    </Nav.Item>
  </Nav>
  </Navbar.Collapse>
</Navbar>
<Switch>
            
        <Route exact path='/spanav' component={Home}></Route>
        <Route exact path='/' component={Home}></Route>
        <Route  path='/Blogs' component={Blogs}></Route>
        <Route  path='/Events' component={Events}></Route> 
        <Route  path='/Courses' component={Courses}></Route>
        <Route  path='/AboutUs' component={AboutUs}></Route>
        <Route  path='/Signup' component={Signup}></Route>
        
    </Switch>
</div>
);
    

    export default Navigbar
