import React from 'react'

import {
    BrowserRouter as Router,
    Switch,
    Route,
    useParams,
  } from "react-router-dom";
  import { Navbar,Nav,NavDropdown,Form,FormControl,Button } from 'react-bootstrap'
  import Home from './Home';
  import AboutUs from './AboutUs';
  import ContactUs from './ContactUs';

class BootstrapNavbar extends React.Component{

    render(){
        return(
            <div>
                <div className="row">
                    <div className="col-md-12">
                        <Router>
                            <Navbar expand="lg" sticky="top" style = {{
                backgroundColor: '#F5F5F5',color:'#00000080', fontSize:'15px',fontWeight:'bold',height:''
            
              }}>
                                <Navbar.Brand href="#home"><img style={{height:'50px'}}src="https://tejaswi-spanav.github.io/spanav/logo.png"></img></Navbar.Brand>
                                <Navbar.Toggle aria-controls="basic-navbar-nav" />
                                <Navbar.Collapse id="basic-navbar-nav"  >
                                    <Nav className="mr-auto">
                                    
                                   
                                    </Nav>
                                    <Nav.Link href="/" style={{color:'#00000080'}}>HOME</Nav.Link>
                                    <Nav.Link href="/about-us" style={{color:'#00000080'}}>COURSES</Nav.Link>
                                    <Nav.Link href="/contact-us" style={{color:'#00000080'}}>BLOGS</Nav.Link>
                                    <Nav.Link href="/contact-us" style={{color:'#00000080'}}>EVENTS</Nav.Link>
                                    <Nav.Link href="/contact-us" style={{color:'#00000080'}}>ABOUT US</Nav.Link>
                                    <Nav.Link href="/contact-us" style={{color:'#00000080'}}>CONTACT US</Nav.Link>
                                    
                                    <NavDropdown  style={{color:'#00000080'}} title="BROWSE BY STREAM"  >
                                        <NavDropdown.Item href="">COMMING SOON</NavDropdown.Item>
                                      
                                    </NavDropdown>

                                    
                                   
                                    
                                    <Button variant="warning">SIGNUP/LOGIN</Button>
                                    
                                    
                                </Navbar.Collapse>
                            </Navbar>
                            <br />
                            <Switch>
                                <Route exact path="/">
                                    <Home />
                                </Route>
                                <Route path="/about-us">
                                    <AboutUs />
                                </Route>
                                <Route path="/contact-us">
                                   
                                </Route>
                            </Switch>
                        </Router>
                    </div>
                </div>
            </div>
        )  
    }
}

export default BootstrapNavbar;